import pixelToFrame from '../frames/draw';


let action = 'bucket';
const paint = document.querySelector('.bucket');
paint.classList.add('highlight');

const currentColorElem = document.getElementsByClassName('current-color')[0];
let currentColor = getComputedStyle(currentColorElem).backgroundColor;
currentColorElem.style.backgroundColor = currentColor;

const prevColorElem = document.getElementsByClassName('prev-color')[0];
prevColorElem.style.backgroundColor = getComputedStyle(prevColorElem).backgroundColor;

let curr;
function dragStart(e) {
  curr = e.target;
}

function dragDrop(e) {
  const { target } = e;
  const dropParent = target.parentNode;
  const swapChild = dropParent.firstChild;
  const fromParent = curr.parentNode;
  fromParent.removeChild(curr);
  dropParent.removeChild(swapChild);

  dropParent.appendChild(curr);
  fromParent.appendChild(swapChild);
}


function move() {
  if (action !== 'move') return;

  const squares = document.querySelectorAll('.figure');
  Array.from(squares).forEach((it) => {
    // eslint-disable-next-line no-param-reassign
    it.draggable = true;
    // eslint-disable-next-line no-param-reassign
    it.ondragstart = dragStart;
    // eslint-disable-next-line no-param-reassign
    it.ondragover = () => false;
    // eslint-disable-next-line no-param-reassign
    it.ondrop = dragDrop;
  });
}


function highlight(activeTool, toolParent) {
  Array.from(toolParent.children).forEach((tool) => {
    tool.classList.remove('highlight');
  });
  activeTool.classList.add('highlight');
}

function chooseTool(event) {
  const activeTool = event.target.closest('div');
  if (activeTool.classList[0] === 'tool') return;
  const toolParent = event.target.closest('.tool');
  action = activeTool.className;
  highlight(activeTool, toolParent);
}

function transform(figureId) {
  const elem = document.getElementById(figureId);
  elem.classList.toggle('change_figure');
}

let frame = Array.from({ length: 9 }, () => 'lightgrey');


function reDrawBigFrame() {
  frame.map((it, ind) => {
    const elem = document.getElementById(ind.toString(10));
    elem.style.backgroundColor = it;
    return it;
  });
}

export default function setFrame(newFrame) {
  frame = newFrame;
  reDrawBigFrame();
}


function bucket(figureId) {
  const elem = document.getElementById(figureId);
  elem.style.backgroundColor = currentColor;
  frame = frame.map((it, ind) => {
    if (ind === parseInt(figureId, 10)) {
      return currentColor;
    }
    return it;
  });
  pixelToFrame(frame);
}

function applyAction(event) {
  const figure = event.target.closest('.figure');
  if (!figure) return;
  const square = event.target.id;
  switch (action) {
    case 'transform': transform(square);
      break;
    case 'bucket': bucket(square);
      break;
    default:
      break;
  }
}


function chooseColor(event) {
  if (action !== 'choose-color') return;
  if (event.target.classList[1] !== 'pick-color') return;
  const newColor = getComputedStyle(event.target).backgroundColor;
  prevColorElem.style.backgroundColor = currentColor;
  currentColorElem.style.backgroundColor = newColor;
  currentColor = newColor;
}

const tool = document.querySelector('.tool');
tool.addEventListener('click', chooseTool);

const color = document.querySelector('.color');
color.addEventListener('click', chooseColor);

document.body.addEventListener('click', applyAction);

const canvas = document.querySelector('.canvas');
canvas.addEventListener('mousedown', move);
