export const pixelFrame = [];

export function chunk(originalArray, chunkCount) {
  return new Array(Math.ceil(originalArray.length / chunkCount)).fill(0)
    .map((_, index) => originalArray.slice(index * chunkCount, index * chunkCount + chunkCount));
}

export function frameFocus(frame) {
  const frameParent = frame.parentNode.parentNode;
  Array.from(frameParent.children).forEach((fr) => {
    fr.firstChild.classList.remove('frame-focus');
  });
  frame.classList.add('frame-focus');
}

function getActiveIndex() {
  const activeFrame = document.querySelector('.frame-focus');
  const frameChildren = activeFrame.parentNode.parentNode.childNodes;

  let activeInd;
  Array.from(frameChildren).filter((it, ind) => {
    if (it.firstChild.classList[1]) {
      activeInd = ind;
    }
    return it;
  });
  return activeInd;
}


export function getActiveFrame() {
  return pixelFrame[getActiveIndex()];
}

export default function pixelToFrame(frame) {
  const activeFrame = document.querySelector('.frame-focus');
  const activeInd = getActiveIndex();

  const frameItem = document.createElement('canvas');
  frameItem.className = 'frame';
  frameItem.setAttribute('width', '150px');
  frameItem.setAttribute('height', '150px');
  const ctx = frameItem.getContext('2d');

  const x = 50;
  const y = 50;
  const size = 49;
  const frameToDraw = chunk(frame, 3);
  frameToDraw.forEach((row, rowInd) => {
    row.forEach((pixel, ind) => {
      ctx.fillStyle = pixel;
      ctx.fillRect(x * ind, y * rowInd, size, size);
    });
  });

  const parent = activeFrame.parentNode;
  parent.firstChild.remove();
  parent.insertBefore(frameItem, parent.children[0]);

  frameFocus(frameItem);

  pixelFrame[activeInd] = frameToDraw;
}
