import setFrame from '../tools';
import {
  chunk, frameFocus, pixelFrame, getActiveFrame,
} from './draw';

const framesSection = document.querySelector('.frames');

let draw;

function frameOnFocus(e) {
  const { target } = e;
  if (target.className !== 'frame') return;
  frameFocus(target);
  setFrame(getActiveFrame().flat());
}

function delFrame(ind) {
  pixelFrame.splice(ind, 1);
  draw();
}

function duplicateFrame(ind) {
  pixelFrame.splice(ind, 0, pixelFrame[ind]);
  draw();
}

function wrapFrame(frame, ind) {
  const frameWrapper = document.createElement('div');
  frameWrapper.className = 'frame-wrapper';
  frameWrapper.appendChild(frame);

  const del = document.createElement('button');
  del.className = 'del-frame';
  del.innerText = 'x';
  frameWrapper.appendChild(del);

  del.addEventListener('click', () => {
    delFrame(ind);
  });

  const twice = document.createElement('button');
  twice.className = 'twice-frame';
  twice.innerText = 'x2';
  frameWrapper.appendChild(twice);

  twice.addEventListener('click', () => {
    duplicateFrame(ind);
  });

  return frameWrapper;
}

draw = () => {
  const frameS = document.querySelector('.frames_section');
  frameS.remove();

  const frSection = document.createElement('div');
  frSection.className = 'frames_section';
  frSection.addEventListener('click', frameOnFocus);

  let frameItem;

  pixelFrame.forEach((frame, frameInd) => {
    frameItem = document.createElement('canvas');
    frameItem.className = 'frame';
    frameItem.setAttribute('width', '150px');
    frameItem.setAttribute('height', '150px');
    const ctx = frameItem.getContext('2d');

    const x = 50;
    const y = 50;
    const size = 49;
    frame.forEach((row, rowInd) => {
      row.forEach((pixel, ind) => {
        ctx.fillStyle = pixel;
        ctx.fillRect(x * ind, y * rowInd, size, size);
      });
    });

    const wrapped = wrapFrame(frameItem, frameInd);
    frSection.appendChild(wrapped);
  });

  const frameParent = document.querySelector('.frames');
  frameParent.insertBefore(frSection, frameParent.children[0]);

  if (pixelFrame.length) {
    frameFocus(frameItem);
  }
};

function addFrame() {
  const newFrame = Array.from({ length: 9 }, () => 'lightgrey');
  const frame = chunk(newFrame, 3);
  pixelFrame.push(frame);
  setFrame(newFrame);
  draw();
}

addFrame();

const btnAdd = document.querySelector('.add-frame');
btnAdd.addEventListener('click', addFrame);

framesSection.addEventListener('click', frameOnFocus);
