import { pixelFrame } from '../frames/draw';

const canvas = document.querySelector('.preview_canvas');
const ctx = canvas.getContext('2d');

canvas.height = 238;
canvas.width = 238;

function drawOneFrame(frame) {
  const x = 80;
  const y = 80;
  const size = 79;
  frame.forEach((row, rowInd) => {
    row.forEach((pixel, ind) => {
      ctx.fillStyle = pixel;
      ctx.fillRect(x * ind, y * rowInd, size, size);
    });
  });
}

const slider = document.querySelector('.slider');
const fpsValue = document.querySelector('.fps-value');
let fps = slider.value;
let speed = 1000 / fps;
slider.addEventListener('input', () => {
  fps = slider.value;
  speed = 1000 / fps;
  fpsValue.innerText = `FPS: ${fps}`;
});

let step = 0;

function animate() {
  if (pixelFrame && pixelFrame.length > 0) {
    if (pixelFrame[step]) {
      drawOneFrame(pixelFrame[step]);
      step += 1;
    } else {
      drawOneFrame(pixelFrame[0]);
      step = 1;
    }
  }
  setTimeout(() => window.requestAnimationFrame(animate), speed);
}

animate();


const gotoFullScreen = () => {
  if (!document.fullscreenElement) {
    const previewElement = document.querySelector('.preview');
    previewElement.webkitRequestFullScreen();
  } else if (document.exitFullscreen) {
    document.exitFullscreen();
  }
};

const fullScreenBtn = document.querySelector('.preview_fullscreen');
fullScreenBtn.addEventListener('click', gotoFullScreen);
